@use './_reset';
@use './_mixin' as *;
@use './_common' as c;

body{
    &.start{
        .yuw_logo,
        .pc-anchor_wrap,
        .pc_txt{
            opacity: 1;
        }
    }
}

.yuw_logo{
    opacity: 0;
    transition: opacity 1s 2s ease-in;
    position: fixed;
    z-index: 40;
    top: 0;
    left: 0;
    width: calc(195 / c.$pcw) * 100%;
    max-width: 92.5px;
    margin: calc(104 / c.$pcw) * 100% 0 0 calc(107 / c.$pcw) * 100%;
}


.contents-inner{
    width: 100%;
    position: relative;
    z-index: 1000;
}

.pc_txt{
    opacity: 0;
    transition: opacity 1s 2s ease-in;
    position: fixed;
    width: calc(41.01 / c.$pcw) * 100%;
    max-width: 20.5px;
    top: 0;
    right: 0;
    margin-top: calc(157 / c.$pcw) * 100%;
    margin-right: calc(73 / c.$pcw) * 100%;
}

.wrapper{
    @include pc{
    width: calc(1342 / c.$pcw) * 100%;
    max-width: 671px;
    position: absolute;
    top: 0;
    right: 0;
    margin-right: calc(303 / c.$pcw) * 100%;
    border-left: solid 1px #FF56D2;
    border-right: solid 1px #FF56D2;
    background-color: rgba(255, 255, 255, 0.3);

    }
}


.pc-anchor_wrap{
    opacity: 0;
    transition: opacity 1s 2s ease-in;
    position: fixed;
    bottom: 0;
    left: 0;
    margin-bottom: calc(149.65 / c.$pcw) * 100%;
    margin-left: calc(123.22 / c.$pcw) * 100%;
    display: none;
    width: -webkit-fill-available;
    @include pc{
        display: flex;
    }

    .anchor_link{
        white-space: nowrap;
        li{
            margin-bottom: 28.5px;
            position: relative;
            padding-bottom: 4px;
            
            &:last-of-type{
                margin-bottom: 0;
            }
            a{
                color: #000;
            }
            .wave{
                position: absolute;
                width: 100%;
                bottom: 0;
                left: 0;
                clip-path: inset(0 100% 0 0);
                transition: all .4s ease;
            }

        }
    }
    .second_link{
        padding-left: calc(97 / c.$pcw) * 100%;
    }

}







.contents:has(#sec_1.on) .pc-anchor_wrap .first_line li:nth-child(1) .wave{
    clip-path: inset(0);
}

.contents:has(#sec_1.on) .pc-anchor_wrap .first_line li:not(:nth-child(1)) .wave{
